<template>
  <div class="order">
    <g-header />
    <div class="content">
      <van-search
        placeholder="商品名称/商品编号/订单号"
        shape="round"
        left-icon="data:image/png;base64,"
      />
      <div class="my_nav">
        <div class="my_nav_inner">
          <van-tabs v-model="activeTab" :border="false">
            <van-tab title="全部">
              <div class="order-empty" v-if="order_all.length===0">
                <img src="@/assets/img/order.png" />
                <div class="desc">您暂时没有相关订单！</div>
              </div>
              <van-card v-for="(item, index) in order_all" :key="index">
                <template #title>
                  <div class="header">
                    <div class="icon">
                      <img :src="item.icon" />
                    </div>
                    <span class="title">{{item.shopName}}</span>
                    <span class="order_state">{{item.status|filterStatus}}</span>
                    <span class="order_box_hd_del"></span>
                  </div>
                  <div class="oi_content">
                    <div class="cover">
                      <div class="wqvue-image">
                        <img :src="item.img" />
                      </div>
                    </div>
                    <div class="content">
                      <div class="desc">{{item.desc}}</div>
                    </div>
                  </div>
                  <div class="order_total_bar">
                    <div class="total_count">共{{item.num}}件商品</div>
                    <div class="payment">
                      实付金额：
                      <span class="price">￥{{item.num*item.price}}</span>
                    </div>
                  </div>
                  <div class="order_btn">
                    <div class="onBuyAgain">再次购买</div>
                    <div class="comment">去评价</div>
                  </div>
                </template>
              </van-card>
              <van-overlay :show="boardIsShow" @click="boardIsShow = false" duration="0" />
            </van-tab>
            <van-tab title="待付款">
              <div class="order-empty">
                <img src="@/assets/img/order.png" />
                <div class="desc">您暂时没有相关订单！</div>
              </div>
              <van-overlay :show="boardIsShow" @click="boardIsShow = false" duration="0" />
            </van-tab>
            <van-tab title="待收货">
              <div class="order-empty">
                <img src="@/assets/img/order.png" />
                <div class="desc">您暂时没有相关订单！</div>
              </div>
              <van-overlay :show="boardIsShow" @click="boardIsShow = false" duration="0" />
            </van-tab>
            <van-tab title="已完成">
              <div class="order-empty">
                <img src="@/assets/img/order.png" />
                <div class="desc">您暂时没有相关订单！</div>
              </div>
              <van-overlay :show="boardIsShow" @click="boardIsShow = false" duration="0" />
            </van-tab>
          </van-tabs>
          <i class="my_nav_arrow" @click="boardIsShow = !boardIsShow" :class="{active:boardIsShow}"></i>
          <div class="my_nav_board" v-if="boardIsShow">
            <div class="my_nav_cate">
              <div class="my_nav_cate_item" v-for="(item, index) in orderCategories" :key="index">
                <div class="my_nav_cate_item_inner">{{item}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GHeader from '../header'

export default {
  data () {
    return {
      activeTab: 0,
      activeName: 0,
      orderCategories: [
        '话费充值',
        '流量充值',
        '加油卡充值',
        '游戏充值',
        '我的电影票',
        '我的火车票',
        '我的机票',
        '我的酒店',
        '医药服务',
        '本地生活服务',
        '全新整车',
        '彩票订单',
        '度假',
        '常购清单',
        '回收站'
      ],
      boardIsShow: false,
      order_all: []
    }
  },
  filters: {
    filterStatus (val) {
      if (val === 0) {
        return '待付款'
      } else if (val === 1) {
        return '待收货'
      } else {
        return '已签收'
      }
    }
  },
  components: {
    GHeader
  },
  methods: {
    async getOrderList () {
      const res = await this.$http.get('/api/orderList')
      this.order_all = res.message
      console.log(res)
    }
  },
  beforeCreate () {
    document.getElementsByTagName('body')[0].style.backgroundColor = '#f7f7f7'
  },
  created () {
    this.getOrderList()
  }
}
</script>

<style lang="less" scoped>
.order {
  .order-empty {
    // padding-bottom: 292px;
    box-sizing: border-box;
    text-align: center;
    padding-top: 100px;
    padding-left: 43px;

    img {
      display: inline-block;
      width: 90px;
      height: 90px;
    }

    .desc {
      margin-top: 12px;
      font-size: 16px;
      color: #666;
      letter-spacing: 0.41px;
      line-height: 24px;
    }
  }

  .van-card {
    position: relative;
    color: #323233;
    font-size: 12px;
    box-sizing: content-box;
    margin: 15px;
    margin-left: 12px;
    background: #fff;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.05);
    width: 100%;
    &__content {
      display: block;
    }

    &__content > div {
      font-family: 'microsoft YaHei';
      .header {
        line-height: 24px;
        position: relative;
        -webkit-box-align: center;
        align-items: center;
        display: flex;

        .icon {
          width: 20px;
          height: 20px;
          display: inline-block;
          margin-right: 5px;
          vertical-align: middle;
        }

        .title {
          font-size: 16px;
          color: #333;
          position: relative;
          max-width: 90%;
          padding-right: 12px;
          display: inline-block;
          vertical-align: middle;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          flex: 1;
        }
        .order_state {
          line-height: 24px;
          flex-shrink: 0;
          font-size: 14px;
          color: #999;
        }
        .order_box_hd_del {
          display: inline-block;
          width: 35px;
          height: 21px;
          position: relative;
          vertical-align: middle;
          flex-shrink: 0;

          &::before {
            content: '';
            position: absolute;
            width: 1px;
            height: 15px;
            background-color: #e3e3e3;
            top: 4px;
            left: 10px;
          }

          &::after {
            content: '';
            position: absolute;
            top: 3px;
            right: 0;
            width: 15px;
            height: 15px;
            background: ~'url(data:image/png;base64,) 50%/15px no-repeat';
          }
        }
      }

      .oi_content {
        display: flex;
        margin-top: 8px;
        overflow: hidden;

        .cover {
          width: 75px;
          height: 75px;
          margin-right: 10px;
          border-radius: 6px;
          flex-shrink: 0;
          position: relative;

          .wqvue-image {
            width: 100%;
            height: 100%;
            border-radius: 6px;
          }
        }

        .content {
          color: #333;
          align-self: center;
          .desc {
            font-size: 14px;
            background-color: #fff;
            color: #333;
            line-height: 20px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
        }
      }
      .order_total_bar {
        padding: 6px 0;
        position: relative;
        display: flex;

        .total_count {
          font-size: 12px;
          color: #999;
          text-align: right;
          line-height: 21px;
          flex: 1;
        }

        .payment {
          margin-left: 10px;
          font-size: 14px;
          color: #999;
          line-height: 21px;

          .price {
            color: #151515;
          }
        }
      }
      .order_btn {
        padding-top: 10px;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
        flex-direction: row-reverse;
        font-size: 14px;
        color: #333;
        line-height: 30px;
        position: relative;
        display: flex;
        &::before {
          content: '';
          height: 0;
          display: block;
          border-top: 1px solid #e5e5e5;
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          transform: scaleY(0.5);
        }
        .onBuyAgain {
          background-color: #fff;
          color: #f2270c;
          border: 1px solid #f2270c;
          box-shadow: 0 3px 6px 0 rgba(242, 39, 12, 0.1);
          text-align: center;
          width: 86px;
          height: 28px;
          line-height: 28px;
          border-radius: 15px;
          margin-left: 8px;
          flex-shrink: 0;
          position: relative;
        }
        .comment {
          background-color: #fff;
          color: #333;
          border: 1px solid #ccc;
          box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.05);
          text-align: center;
          width: 86px;
          height: 28px;
          line-height: 28px;
          border-radius: 15px;
          margin-left: 8px;
          flex-shrink: 0;
          position: relative;
        }
      }
    }
  }
  .content {
    height: 100%;
    background-color: #f7f7f7;
    .van-search {
      border-top: 1px solid #e5e5e5;
      border-bottom: 1px solid #e5e5e5;
      &__content {
        background-color: #f7f7f7;
        ::-webkit-input-placeholder {
          color: #ccc;
        }
      }
    }

    .my_nav {
      position: relative;
      z-index: 8;
      height: 100%;

      &_inner {
        position: relative;
        background-color: #f7f7f7;
        border-radius: 0 0 10px 10px;
        height: 100%;
        padding-right: 43px;
        .van-tabs {
          position: relative;
          height: 100%;
          .van-tab {
            -webkit-box-flex: 1;
            flex: 1;
            text-align: center;
            display: block;
          }
          .van-overlay {
            position: absolute;
            padding-bottom: 100%;
            transition: 0s;
            background-color: rgba(0, 0, 0, 0.3);
            padding-right: 43px;
          }
        }

        .my_nav_arrow {
          position: absolute;
          background-color: #fff;
          right: 0;
          top: 0;
          width: 43px;
          height: 44px;

          &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 16px;
            width: 1px;
            height: 12px;
            background-color: #e5e5e5;
          }

          &::after {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            width: 10px;
            height: 6px;
            margin: -3px 0 0 -5px;
            background: ~'url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAMAgMAAAAfYnVoAAAABGdBTUEAALGPC/xhBQAAAAFzUkdCAK7OHOkAAAAMUExURczMzM7OzkxpcczMzHhv+rkAAAAEdFJOU/5UAOAelWMaAAAAQ0lEQVQI1w3MMREAIQxFwZdhJgquQg12Lh7wEBFp+CqQRpotly1pc0NncKdqsFxppGk5xc/k8BFoYUKJt8VsD9Gqjwdg1RgRk+xAjwAAAABJRU5ErkJggg==) 50%/10px 6px no-repeat';
          }

          &.active {
            &::after {
              transform: rotate(180deg);
            }
          }
        }

        .my_nav_board {
          position: absolute;
          width: 100%;
          padding-bottom: 15px;
          top: 44px;
          left: 0;
          z-index: 10;
          background-color: #f7f7f7;

          .my_nav_cate {
            position: relative;
            padding: 25px 5px 15px;

            &_item {
              overflow: hidden;
              box-sizing: border-box;
              float: left;
              padding: 0 5px;
              margin-bottom: 10px;
              width: 33.33333%;
              height: 30px;
              line-height: 30px;
              font-size: 14px;
              color: #333;
              &_inner {
                text-align: center;
                display: block;
                background: #fff;
                border-radius: 15px;
                height: 30px;
                line-height: 30px;
                overflow: hidden;
                color: #333;
                font-size: 14px;
              }
            }
          }
        }
      }
    }
  }
}
</style>
